import React from "react";
import jQuery from "jquery";
import "./NewsTicker.css";
import announcement from "../components/Data/announcemnet.json";
import newtag from "../images/newtag.png";
import $ from "jquery";
import "jquery-ui-dist/jquery-ui";
import { Link } from "react-router-dom";
const NewsTicker = () => {
  jQuery.fn.liScroll = function (settings) {
    settings = jQuery.extend(
      {
        travelocity: 0.03,
      },
      settings
    );
    return this.each(function () {
      var $strip = jQuery(this);
      $strip.addClass("newsticker");
      var stripHeight = 1;
      $strip.find("li").each(function (i) {
        stripHeight += jQuery(this, i).outerHeight(true); // thanks to Michael Haszprunar and Fabien Volpi
      });
      var $mask = $strip.wrap("<div class='mask'></div>");
      var $tickercontainer = $strip
        .parent()
        .wrap("<div class='tickercontainer'></div>");
      var containerHeight = $strip.parent().parent().height(); //a.k.a. 'mask' width
      $strip.height(stripHeight);
      var totalTravel = stripHeight;
      var defTiming = totalTravel / settings.travelocity; // thanks to Scott Waye
      function scrollnews(spazio, tempo) {
        $strip.animate({ top: "-=" + spazio }, tempo, "linear", function () {
          $strip.css("top", containerHeight);
          scrollnews(totalTravel, defTiming);
        });
      }
      scrollnews(totalTravel, defTiming);
      $strip.hover(
        function () {
          jQuery(this).stop();
        },
        function () {
          var offset = jQuery(this).offset();
          var residualSpace = offset.top + stripHeight;
          var residualTime = residualSpace / settings.travelocity;
          scrollnews(residualSpace, residualTime);
        }
      );
    });
  };

  $(function () {
    $("ul#ticker01").liScroll();
  });

  return (
    <div class="holder">
      <ul id="ticker01">
        {announcement.map((announcement, index) => {
          return (
            <li key={index}>
              <p>{announcement.title}</p>
              <div>
                <img src={newtag} alt="newtag" className="new-tag" />
                <a href={announcement.link}>{announcement.link}</a>
              </div>
            </li>
          );
        })}
      </ul>
    </div>
  );
};

export default NewsTicker;
