import { Link, useParams } from "react-router-dom";

import CompleteAndContinueButton from "./CompleteButton";
import courses from "./courses";

function Lesson() {
  const { courseId, lessonId } = useParams();
  const course = courses.find((course) => course.id === parseInt(courseId));
  const lesson = course.lessons.find(
    (lesson) => lesson.id === parseInt(lessonId)
  );
  const nextLessonId = () => {
    const currentIndex = course.lessons.indexOf(lesson);
    const nextIndex = (currentIndex + 1) % course.lessons.length;
    return course.lessons[nextIndex].id;
  };

  return (
    <div className="container mt-5 p-5">
      <div className="row">
        <div className="col-md-3">
          <ul class="list-group">
            <li class="list-group-item disabled text-center">
              <h4>Lessons</h4>
            </li>
            {course.lessons.map((lesson, index) => (
              <li class="list-group-item">
                <Link
                  to={`/courses/${course.id}/lessons/${lesson.id}`}
                  className="text-decoration-none text-dark"
                >
                  <span
                    style={{
                      borderRadius: "50%",
                      background: "#f8f9fa",
                      padding: "0.4rem",
                    }}
                  >
                    {index + 1}
                  </span>
                  {lesson.title}
                </Link>
              </li>
            ))}
          </ul>
        </div>
        <div
          className="col-md-6"
          style={{
            border: "2px solid #ccc",
            padding: "1rem",
          }}
        >
          <iframe
            width="600"
            height="345"
            src={lesson.video}
            style={{
              maxWidth: "100%",
            }}
          ></iframe>
          <CompleteAndContinueButton
            courseId={courseId}
            lessonId={nextLessonId()}
          />
          <Link to={"/courses/" + course.id}>
            <button className="btn btn-success mx-3 mt-4">
              Back to {course.title}
            </button>
          </Link>
        </div>
      </div>
    </div>
  );
}

export default Lesson;
