import { useParams } from "react-router-dom";
import LessonSummary from "../courses/LessonSummary";
import { Link } from "react-router-dom";
import courses from "./courses";

function Course() {
  const { courseId } = useParams();
  const course = courses.find((course) => course.id === parseInt(courseId));
  console.log(course.lessons[0].video);

  return (
    <div className="conatiner mt-5 p-5">
      <div
        style={{
          backgroundColor: "#f8f9fa",
          padding: "1rem",
        }}
      >
        <div className="row">
          <div className="col-md-6">
            <h1>{course.title}</h1>
            <p style={{ fontSize: "16px" }}>{course.description}</p>

            <p style={{ fontSize: "20px" }}>
              <b>Created by: </b> {course.by}
            </p>
            <p style={{ fontSize: "20px" }}>
              <b>Last Updated : </b> {course.timeStamp}
            </p>
            <p style={{ fontSize: "20px" }}>
              <b>Price: </b> {course.price}
            </p>
            <Link to={"/coursePage"}>
              <button className="btn btn-primary">Back to courses</button>
            </Link>
          </div>
          <div className="col-md-6">
            <img
              src={course.image}
              alt="course"
              style={{ width: "100%", height: "100%" }}
            />
            {/* <iframe
              width="100%"
              height="315"
              src={course.lessons[0].video}
              title="YouTube video player"
              frameborder="0"
              allow="accelerometer; clipboard-write; encrypted-media; gyroscope; picture-in-picture"
              allowfullscreen
            ></iframe> */}
          </div>
        </div>
      </div>
      <h3 className="mt-3 text-center">
        Showing {course.lessons.length} Lessons{" "}
      </h3>
      {course.lessons.map((lesson, index) => (
        <LessonSummary
          courseId={courseId}
          lesson={lesson}
          num={index + 1}
          key={lesson.id}
        />
      ))}
    </div>
  );
}

export default Course;
