import axios from "axios";
import React, { useState, useEffect } from "react";
import { toast } from "react-toastify";

const AddJob = () => {
  const [job, setJob] = useState({
    title: "",
    subTitle: "",
    role: "",
    experience: "",
    level: "",
    location: "",
    jobStat: "",
    jobDays: "",
    companyName: "",
    //count days from the date of posting
    date: "",
    //count no of applicants
    applicants: "",
    salary: "",
    //count no of views
    views: "",
    //count no of shares
    shares: "",
    overview: "",

    logo: "",
  });
  //merge job and jobDesc

  console.log(job);
  //.........................
  const [addJobDec, setAddJobDec] = useState([]);
  const [jobDesc, setJobDesc] = useState([]);

  const handleAddJobDesc = (e) => {
    e.preventDefault();
    //count of problem statement
    const count = addJobDec.length;
    // adding problem statement
    jobDesc.push({
      description: "",
    });
    console.log("setpf", count, jobDesc);
    setAddJobDec([
      ...addJobDec,

      <>
        <div className="p-3">
          <div class="form-group">
            <label>Description</label>
            <textarea
              id="mytextarea"
              class="form-control"
              rows="3"
              placeholder="Enter the description of problemStatement"
              name={`description${count}`}
              onChange={(e) => {
                // setpDescription((prevState) => {
                //   var val = e.target.value;
                //   return { ...prevState, [e.target.name]: val };
                // });
                setJobDesc((prevState) => {
                  var val = e.target.value;
                  jobDesc[count].description = val;
                  return jobDesc;
                });
              }}
            />
          </div>
        </div>
      </>,
    ]);
  };

  const handleChange = (e) => {
    setJob({ ...job, [e.target.name]: e.target.value });
  };
  useEffect(() => {
    setJob({ ...job, jobDesc });
    console.log("job", job);
  }, [jobDesc]);

  const handleSubmit = (e) => {
    e.preventDefault();
    console.log("job", job);
    axios
      .post(`/api/createJobs`, job)

      .then((res) => {
        toast.success("Job added successfully");
        console.log(res);
      })
      .catch((err) => {
        console.log(err);
      });
  };

  //create a form to add a job

  return (
    <div class="container">
      <div class="row mx-0 justify-content-center">
        <div
          class="col-md-6 col-lg-5"
          style={{
            width: "100%",
          }}
        >
          <form class="rounded-1 p-4 border bg-white" onSubmit={handleSubmit}>
            <label class="d-block mb-4">
              <span class="form-label d-block">
                Title<span class="text-danger">*</span>
              </span>
              <input
                name="title"
                type="text"
                onChange={handleChange}
                class="form-control"
                placeholder="Enter the title of the job"
              />
            </label>

            <label class="d-block mb-4">
              <span class="form-label d-block">
                SubTitle<span class="text-danger">*</span>
              </span>
              <input
                name="subTitle"
                type="text"
                class="form-control"
                onChange={handleChange}
                placeholder=" Enter the subtitle of the job"
              />
            </label>

            <label class="d-block mb-4">
              <span class="form-label d-block">
                Role<span class="text-danger">*</span>
              </span>
              <input
                name="role"
                type="text"
                onChange={handleChange}
                class="form-control"
                placeholder=""
              />
            </label>

            <label class="d-block mb-4">
              <span class="form-label d-block">
                Experience<span class="text-danger">*</span>
              </span>
              <input
                name="experience"
                type="text"
                onChange={handleChange}
                class="form-control"
                placeholder=""
              />
            </label>

            <label class="d-block mb-4">
              <span class="form-label d-block">
                Level<span class="text-danger">*</span>
              </span>
              <input
                name="level"
                type="text"
                onChange={handleChange}
                class="form-control"
                placeholder=""
              />
            </label>

            <label class="d-block mb-4">
              <span class="form-label d-block">
                Location<span class="text-danger">*</span>
              </span>
              <input
                name="location"
                type="text"
                onChange={handleChange}
                class="form-control"
                placeholder=""
              />
            </label>

            <label class="d-block mb-4">
              <span class="form-label d-block">
                Job Status<span class="text-danger">*</span>
              </span>
              <input
                name="jobStat"
                type="text"
                onChange={handleChange}
                class="form-control"
                placeholder=""
              />
            </label>
            <label class="d-block mb-4">
              <span class="form-label d-block">
                Applicants<span class="text-danger">*</span>
              </span>
              <input
                name="applicants"
                type="text"
                onChange={handleChange}
                class="form-control"
                placeholder=""
              />
            </label>

            <label class="d-block mb-4">
              <span class="form-label d-block">
                Job Days<span class="text-danger">*</span>
              </span>
              <input
                name="jobDays"
                type="text"
                onChange={handleChange}
                class="form-control"
                placeholder=""
              />
            </label>
            <label class="d-block mb-4">
              <span class="form-label d-block">
                Company Name<span class="text-danger">*</span>
              </span>
              <input
                name="companyName"
                type="text"
                onChange={handleChange}
                class="form-control"
                placeholder=""
              />
            </label>
            <label class="d-block mb-4">
              <span class="form-label d-block">
                Overview<span class="text-danger">*</span>
              </span>
              <input
                name="overview"
                type="text"
                onChange={handleChange}
                class="form-control"
                placeholder=""
              />
            </label>

            <label class="d-block mb-4">
              <span class="form-label d-block">
                Salary<span class="text-danger">*</span>
              </span>
              <input
                name="salary"
                type="text"
                onChange={handleChange}
                class="form-control"
                placeholder=""
              />
            </label>

            <label class="d-block mb-4">
              <span class="form-label d-block">
                Date <span class="text-danger">*</span>
              </span>
              <input
                name="date"
                type="text"
                onChange={handleChange}
                class="form-control"
                placeholder=""
              />
            </label>
            <label class="d-block mb-4">
              <span class="form-label d-block">
                Logo <span class="text-danger">*</span>
              </span>
              <input
                name="logo"
                type="text"
                onChange={handleChange}
                class="form-control"
                placeholder="Enter url of the logo"
              />
            </label>

            <div class="form-group p-1">
              <button onClick={handleAddJobDesc} className="btn btn-main mt-2">
                <i className="fab fa-plus"></i>Add Job Description
              </button>
              {addJobDec}
            </div>
            <div class="mb-3">
              <button type="submit" class="btn btn-primary px-3 rounded-3">
                Save
              </button>
            </div>
          </form>
        </div>
      </div>
    </div>
  );
};

export default AddJob;
